<!-- 客户留言 -->
<template>
	<div>
		<div class="contactImg">
			<div class="bannerContact">
				<img src="../ContactUs/img/banner-Contact.png">
			</div>
		</div>
		
		<div class="cantactCantainer">
			<div class="cantactCantainer2">
				<LoginHeader title="客户留言" :tips="tips"></LoginHeader>
				
				<div class="ContactBody">
					<PageLeft :entry="entry"></PageLeft>
					<div class="LeaveRight">
						<div>
							客户留言
						</div>
						
						<div>
							<h1>哈哈哈汉堡</h1>
							留言成功后，哈哈哈汉堡将第一时间为您回电解答，使您对哈哈哈汉堡公司
							<br>
							获得更多的了解，迈向您成功投资创业的第一步
							<div></div>
						</div>
						
						<div>
							<table>
								<tbody>
									<tr>
										<th>请问您贵姓</th>
										<td>
											<input type="text" />
											性别：
											<select>
												<option>请选择</option>
												<option>男</option>
												<option>女</option>
											</select>
										</td>
										<th>联系电话</th>
										<td><input type="text" /></td>
									</tr>
									<tr>
										<th>接受开店资料</th>
										<td><input type="checkbox" /></td>
										<th>接受资料QQ</th>
										<td><input type="text" /></td>
									</tr>
									<tr>
										<th>请选择您的地区</th>
										<td colspan="3">
											<select>
												<option>请选择</option>
											</select>
											<select>
												<option>请选择</option>
											</select>
										</td>
									</tr>
									<tr>
										<th>留言内容</th>
										<td colspan="3">
											<select>
												<option>请输入快捷留言内容</option>
											</select>
											<label>
												<textarea></textarea>
											</label>
											<p>
												<button type="button" class="btn btn-info">提交留言</button>
											</p>
										</td>
									</tr>
								</tbody>
							</table>
						</div>
					</div>
				</div>
				
				<LoginFoot></LoginFoot>
			</div>
		</div>
	</div>
</template>

<script>
import LoginHeader from "../../components/LoginHeader/LoginHeader.vue"
import LoginFoot from "../../components/LoginFoot/LoginFoot.vue"
import PageLeft from "../../components/PageLeft/PageLeft.vue"
export default{
	components:{
		LoginHeader,
		LoginFoot,
		PageLeft
	},
	data:function(){
		return{
			tips:["哈哈哈汉堡","联系我们","客户留言"],
			entry:[
				{
					message:"联系方式",
					hover:0,
					skip:"/contactus"
				},
				{
					message:"客户留言",
					hover:1,
					skip:"/leavewords"
				}
			]
		}
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.LeaveRight
	float right
	width 80%
	div:nth-child(1)
		font-size: 20px;
		border-bottom: 1px dashed #c19671;
		margin-bottom: 25px;
	div:nth-child(2)
		color #900
		height 200px
		font-size 15px
		position relative
		h1
			font-size 20px
		div
			display inline-block
			width 176px
			height 152px
			position absolute
			right 60px
			top 0
			background url("./img/edit.png")
	div:nth-child(3)
		tr
			height 35px
			font-size 15px
			th
				text-align right
				padding 5px
			td
				padding 5px
				input,select,textarea
					border 1px solid #dfc1a7
				label
					display block
					margin-top 10px
					width 355px
					height 105px
					textarea
						width 355px
						height 105px
						resize none//去掉右下角横线
		tr:nth-child(4)
			select
				width 355px
</style>


