<!-- 汉堡加盟 -->
<template>
	<div>
		<div class="contactImg">
			<div class="bannerContact">
				<img src="./img/banner-Affiliate.png">
			</div>
		</div>
		
		<div class="hamburgCantainer">
			<div class="hamburgCantainer2">
				<LoginHeader title="汉堡加盟" :tips="tips"></LoginHeader>
				
				<div class="hamburgBody">
					<PageLeft :entry="entry"></PageLeft>
					<div class="hamburgRight">
						<div>汉堡加盟</div>
						
						<div>
							<div>
								<h1>创富加盟，联手共赢</h1>
								<p>哈哈哈采用成本集约化管理，确保原材料价格最低</p>
								<p>全方位整合优势资源，从而合经营者利润高达<span>60%</span></p>
							</div>
							
							<p>1、品牌优势：西式快餐的主要消费群体是青少年儿童，公司针对消费者的年龄构成特点注册了“哈哈哈”品牌，使其更能贴近我们的消费者，据对已开的哈哈哈门店的调查显示，消费者 对“哈哈哈”的认知度明显高于其他同类品牌，特别是小朋友更容易记住和传唱这个名字， 这样大大提高了品牌影响力，降低门店广告投入的费用。</p>
							<p>2、成本优势：公司拥有统一产品供应商，总部采取集团化采购，既能严格保证产品品质的同时， 又能最大限度的降低成本，让广大加盟店享受最大的产品成本优势，更好的获得成功。</p>
							<p>3、经验优势：公司通过自营或特许经营多年积累下来的成功经验，经过更新或复制，让加盟商少走弯路，节约摸索时间和投资成本。</p>
							<p>4、地域考察优质：加盟商一旦确定加盟区域的店面位置，即可向公司申请派专业的市场评测人员对此市场进行实地考察评估和运营策划。</p>
							<p>5、技术优势：加盟店将享有专业统一的技术培训。培训工作一般在开业前10天左右进行，加盟店可安排人员到上海总部接受系统的技术培训和运营管理上的指导，也可向公司申请派专业技 术人员上门培训，培训费全免。</p>
							<p>6、统计优势：门店一旦开业就要建立标准的核算体系，以降低运营成本、减少浪费和方便管理。</p>
						</div>
					</div>
				</div>
				
				<LoginFoot></LoginFoot>
			</div>
		</div>
	</div>
</template>

<script>
import LoginHeader from "../../components/LoginHeader/LoginHeader.vue"
import LoginFoot from "../../components/LoginFoot/LoginFoot.vue"
import PageLeft from "../../components/PageLeft/PageLeft.vue"
export default{
	components:{
		LoginHeader,
		LoginFoot,
		PageLeft
	},
	data:function(){
		return{
			tips:["哈哈哈汉堡","汉堡加盟","投资分析"],
			entry:[
				{
					message:"投资分析",
					hover:1,
					skip:""
				},
				{
					message:"加盟优势",
					hover:0,
					skip:""
				},
				{
					message:"加盟条件",
					hover:0,
					skip:""
				},
				{
					message:"加盟方案",
					hover:0,
					skip:""
				},
				{
					message:"加盟流程",
					hover:0,
					skip:""
				},
				{
					message:"后续支持",
					hover:0,
					skip:""
				},
				{
					message:"加盟问答",
					hover:0,
					skip:""
				}
			]
		}
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.hamburgCantainer
	width 100%
	height 950px
	background url("../../../static/img/container.jpg")
	.hamburgCantainer2
		width 58%
		height 950px
		margin 0 auto
		.hamburgBody
			padding 20px 55px 20px 40px
			width 100%
			height 790px
			background url("../../../static/img/c2.jpg") repeat-y
			.hamburgRight
				width 80%
				float right
				div:nth-child(1)
					font-size 20px
					border-bottom 1px dashed #c19671
					margin-bottom 25px
				div:nth-child(2)
					div
						background url("./img/img4.png") no-repeat right top
						height 240px
						padding-top 20px
						h1
							margin-bottom 35px
						p
							font-size 15px
							margin 0
							span
								font-size 35px
								color #900
								vertical-align middle
								font-style italic
					p
						font-size 15px
						margin 5px 0
						line-height 30px
</style>


