<!-- 奶茶加盟 -->
<template>
	<div>
		<div class="contactImg">
			<div class="bannerContact">
				<img src="./img/banner-Milk_tea.png">
			</div>
		</div>
		
		<div class="milkTeaCantainer">
			<div class="milkTeaCantainer2">
				<LoginHeader title="奶茶加盟" :tips="tips"></LoginHeader>
				
				<div class="milkTeaBody">
					<PageLeft :entry="entry"></PageLeft>
					<div class="milkTeaRight">
						<div>
							奶茶介绍
						</div>
						
						<div>
							<p>&nbsp;</p>
							<img src="./img/img5.png" />
							<img src="./img/img6.png" />
							<p>&nbsp;</p>
							<img src="./img/img7.png" />
							<p>&nbsp;</p>
							<img src="./img/img8.png" />
							<p>&nbsp;</p>
							<p>口味不断推陈出新，变化多，形成众多系列，消费者选择广，仅仅奶茶就有茉香奶茶，咖啡红奶茶，麦香红茶奶茶等等，添加物就有珍珠、椰果，还有雪泡、沙冰、刨冰等!</p>
							<p>投资少、风险低、见效快，奶茶即可万元投资开间小店，一般在商业街、步行街、广场周边、住宅小区、学校等 人口密度大的地区都可经营，让奶茶饮品开店选址得以灵活化！</p>
						</div>
					</div>
				</div>
				
				<LoginFoot></LoginFoot>
			</div>
		</div>
	</div>
</template>

<script>
import LoginHeader from "../../components/LoginHeader/LoginHeader.vue"
import LoginFoot from "../../components/LoginFoot/LoginFoot.vue"
import PageLeft from "../../components/PageLeft/PageLeft.vue"
export default{
	components:{
		LoginHeader,
		LoginFoot,
		PageLeft
	},
	data:function(){
		return{
			tips:["哈哈哈汉堡","奶茶加盟","奶茶介绍"],
			entry:[
				{
					message:"奶茶介绍",
					hover:1,
					skip:""
				}
			]
		}
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.milkTeaCantainer
	width 100%
	height 1900px
	background url("../../../static/img/container.jpg")
	.milkTeaCantainer2
		width 58%
		height 1900px
		margin 0 auto
		.milkTeaBody
			padding 20px 55px 20px 40px
			width 100%
			height 1760px
			background url("../../../static/img/c2.jpg") repeat-y
			.milkTeaRight
				float right
				width 80%
				div:nth-child(1)
					font-size 20px
					border-bottom 1px dashed #c19671
					margin-bottom 25px
				div:nth-child(2)
					p:nth-child(9),p:nth-child(10)
						color #660000
						font-size 15px
</style>


