<!-- 认识我们 -->
<template>
	<div>
		<div class="contactImg">
			<div class="bannerContact">
				<img src="./img/banner-about.png">
			</div>
		</div>
		
		<div class="knowCantainer">
			<div class="knowCantainer2">
				<LoginHeader title="认识我们" :tips="tips"></LoginHeader>
				
				<div class="knowBody">
					<PageLeft :entry="entry"></PageLeft>
					<div class="knowWeRight">
						<div>公司简介</div>
						
						<p>
							<img src="./img/img2.jpg" />
						</p>
						<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;哈哈哈汉堡是中国西式快餐特许加盟、合作、铺展的知名品牌。公司集产品开发、生产、销售为一体。主要经营汉堡系列、炸鸡系列、披萨系列、蛋挞系列、冰淇淋系列、冰品系列、果汁系列、奶茶系列，咖啡系列，寿司系列，特色炒饭系列，港式煲仔饭系列，早餐系列，中式简餐等系列快餐美食。我们公司本着诚信、务实、合作、共赢的经营宗旨，为消费者和广大加盟商 营造出卫生、时尚、便捷的用餐新环境，健康、实惠、美味的用营领域的佼佼者。 哈哈哈汉堡为加盟者提供：当地市场考查、投资分析、选址筹建、装修设计原料采购、技术培训、售后服务等专业全面的支持。</p>
						<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;2011年10月哈哈哈汉堡首次走出国门，在太平洋岛国萨摩亚闪亮登陆；至2011年10月，已在全国发展加盟连锁店近118家，取得了与加盟商互发展共赢利的良好局面，在社会上也树立了良好的品牌知名度和较高信誉度。 我们以强力的品牌支持 ;美味无比的食品;高品质规范化的管理;低投入高回报的显著优势，帮助投资者实现成功经营西式快餐，创造财富的理想。</p>
						<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;欢迎广大消费者和加盟商选择哈哈哈汉堡，您的选择、您的加盟是对我们品牌的支持和信任。心有多大，路就有多广，新的起点，新的航程，选择哈哈哈汉堡，您快乐，我们就快乐。让哈哈哈汉堡与你们同舟共济，共同开创美好生活新篇章！</p>
					</div>
				</div>
				
				<LoginFoot></LoginFoot>
			</div>
		</div>
	</div>
</template>

<script>
import LoginHeader from "../../components/LoginHeader/LoginHeader.vue"
import LoginFoot from "../../components/LoginFoot/LoginFoot.vue"
import PageLeft from "../../components/PageLeft/PageLeft.vue"
export default{
	components:{
		LoginHeader,
		LoginFoot,
		PageLeft
	},
	data:function(){
		return{
			tips:["哈哈哈汉堡","认识我们","公司简介"],
			entry:[
				{
					message:"公司简介",
					hover:1,
					skip:""
				},
				{
					message:"特许经营",
					hover:0,
					skip:""
				},
				{
					message:"企业文化",
					hover:0,
					skip:""
				},
				{
					message:"组织机构",
					hover:0,
					skip:""
				}
			]
		}
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.knowCantainer
	width 100%
	height 950px
	background url("../../../static/img/container.jpg")
	.knowCantainer2
		width 58%
		height 950px
		margin 0 auto
		.knowBody
			padding 20px 55px 20px 40px
			width 100%
			height 790px
			background url("../../../static/img/c2.jpg") repeat-y
			.knowWeRight
				width 80%
				float right
				div:nth-child(1)
					font-size 20px
					border-bottom 1px dashed #c19671
					margin-bottom 25px
				p
					margin-bottom 15px
					line-height 35px
					font-size 14px
</style>


